.action_button {
    position: fixed;
    bottom: 20px;
    right: 20px;
    background: #dc3eb1;
    color: white;
    padding: 15px 40px;
    font-size: 16px;
    font-family: 'Pacifico', cursive;
    border: 3px solid #42b6e6;
    cursor: pointer;
  }
  
  .action_button.btn_2 {
    right: 300px;
  }
  
  
  .result_wrapper {
    text-align: center;
    margin-top: 30px;
  }
  
  .result_wrapper h3 {
    color: #41b6e6;
  }
  .result_wrapper div {
    font-size: 40px;
    color: #585858;
  }